import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController) {

  }
    MenuHelmPage(ket){
      this.navCtrl.push("MenuHelmPage", {kategori:ket});
    }
	MenuApillPage(ket){
      this.navCtrl.push("MenuApillPage", {kategori:ket});
    }
	MenuMarkaPage(ket){
      this.navCtrl.push("MenuMarkaPage", {kategori:ket});
    }
	MenuKecepatanPage(ket){
      this.navCtrl.push("MenuKecepatanPage", {kategori:ket});
    }
	MenuQuizPage(ket){
      this.navCtrl.push("MenuQuizPage", {kategori:ket});
    }
}


